.header {
  text-transform: uppercase;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header .logo {
  width: 40px;
  height: 40px;
}

.header .nav, .header .nav__list {
  gap: 20px;
  list-style: none;
  display: flex;
}

.header .nav__link {
  color: #060b35;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.header .nav__link:hover, .header .nav__link.is-active {
  color: #00acdc;
}

.header .nav__link.is-active:after {
  color: #00acdc;
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
}

.card__image {
  box-sizing: border-box;
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__title {
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  text-align: left;
  color: #616070;
  margin-top: 4px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__text {
  color: #616070;
  font-size: 12px;
}

.card__number {
  font-size: 16px;
  font-weight: 700;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__reviews {
  line-height: 14px;
}

.card__link {
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  margin-top: 16px;
  padding: 12px;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  display: block;
}

.card__link:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover__title {
  color: #34568b;
}

.card:hover__link {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:first-child) {
  margin-left: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card-block {
  color: #060b35;
  background-color: #fff;
  grid-template-columns: 200px;
  justify-content: center;
  gap: 46px 48px;
  margin: 0 auto;
  padding: 50px 40px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  line-height: 18px;
  display: grid;
}

@media screen and (width >= 488px) {
  .card-block {
    grid-template-columns: repeat(2, 200px);
  }
}

@media screen and (width >= 768px) {
  .card-block {
    grid-template-columns: repeat(3, 200px);
  }
}

@media screen and (width >= 1024px) {
  .card-block {
    grid-template-columns: repeat(4, 200px);
  }
}

body, h1, h2, h3, h4, p {
  margin: 0;
}

html {
  font-family: Roboto, sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

